body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.flexo{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    padding: 10%;
}
#card1{
    box-shadow: 5px 5px 5px #aaaaaa;
    border-radius: 5px;
    margin: 1vh;
    
    
}
a{
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
}
#card2{
    box-shadow: 5px 5px 5px #aaaaaa;
    border-radius: 5px;
    margin: 1vh;
    width:15rem;
    height: 20rem;
    color: whitesmoke;    
    background-color: #282c34;
    
    
}
#card3{
    box-shadow: 5px 5px 5px #aaaaaa;
    border-radius: 5px;
    margin: 1vh;
    width:30rem;
    height: 20rem;
    color: whitesmoke;    
    background-color: #282c34;
    
    
}

.App {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  background-color: #eeeeee;
  color:#282c34;
}
html{
  scroll-behavior: smooth;
}
nav{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: nowrap;
          flex-wrap: nowrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: #282c34;
  padding: 2.5vh;
}

.emblem{
 
  max-height: 5vh;
}
.emblem2{
  max-height: 25vh;
}
.navText{
color:whitesmoke;
font-size:4vh; 
margin:1vh;
-webkit-text-decoration-line: none;
        text-decoration-line: none;
}
.text{
  font-size: 5vh;
  padding-top:20vh;
  padding-bottom:20vh;

}
.skills{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  margin-left:20vw;
  margin-right:20vw;
  padding-bottom: 20vh;
  padding-top: 20vh;
}

.button1{
  border-style: solid;
  border-width: 0.25em;
  border-radius: 2em;
  border-color: #282c34;
  padding: 0.5em;
